import React from 'react';
import './content.css';
import Profile from '../../../components/profile';
import After from '../../../assets/movies/after.jpg';
import Archer from '../../../assets/movies/archer.jpg';
import Jumanji from '../../../assets/movies/jumanji.jpg';
import Parasite from '../../../assets/movies/parasite.jpeg';
import Shrek from '../../../assets/movies/shrek.jpg';
import Moonlight from '../../../assets/movies/moonlight.jpg';
import MarriageStory from '../../../assets/movies/mariageStory.png';
import Frontline from '../../../assets/movies/frontLine.jpg';

const DATA = [
    {   
        poster: After,
        name: 'After',
        genre: 'Action',
        rating: 8.6,
    },
    {
        name: 'Archer',
        poster: Archer,
        genre: 'Action',
        rating: 7.5,
    },
    {
        poster: Jumanji,
        name: 'Jumanji',
        genre: 'Comedy, Action',
        rating: 6.2,
    },
    {
        poster: Parasite,
        name: 'Parasite',
        genre: 'Thriller',
        rating: 9.8,
    },
    {
        poster: Shrek,
        name: 'Shrek',
        genre: 'Sci-fi',
        rating: 9.8,
    },
    {
        poster: Moonlight,
        name: 'Moonlight',
        genre: 'Horror',
        rating: 7.2,
    },
    {
        poster: MarriageStory,
        name: 'Marriage Story',
        genre: 'Drama',
        rating: 6.0,
    },
    {
        poster: Frontline,
        name: 'Frontline',
        genre: 'Drama, Thriller',
        rating: 8.7,
    },
];

class Content extends React.Component {
    render() {
        return(
            <div className="content">
                <div className="content-sort">
                    <h2>Sort by</h2>
                    <select className="dropdown-list">
                        <option>Title</option>
                        <option>Genre</option>
                        <option>Rating</option>
                    </select>
                </div>
                <div className="content-movie" style={{display: 'flex', flexWrap: 'wrap'}}>
                {DATA.map(list => <Profile poster={list.poster} name={list.name} genre={list.genre} rating={list.rating}/>)}
                </div>
            </div>
        );
    }
}
export default Content;