import React from 'react';

class MenuSection extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return(
            <div style={{margin: "30px 0"}}>
                <h1>{this.props.sectionTitle}</h1>
                {this.props.children}
            </div>
        );
    }
}
export default MenuSection;